
export default {
  props: {
    coverImage: {
      type: String,
      required: true
    },
    title: {
      type: String,
      required: true
    },
    desp: {
      type: String,
      required: true
    },
    ctaTitle: {
      type: String,
      required: true
    },
    routeName: {
      type: String,
      required: true
    },
    theme: {
      type: String,
      default: 'light'
    }
  },
  computed: {
    cover () {
      return require(`~/assets/imgs/${this.coverImage}`)
    },
    despLines () {
      return this.desp.split('\n')
    },
    to () {
      // default 石化業
      const industry = '03'
      return {
        name: this.routeName,
        params: {
          industry
        }
      }
    }
  }
}
